import React from 'react';
import { useSelector } from 'react-redux';
import { selectCondition } from '../store/conditionSlice';
import moment from 'moment';


function Conditions() {
    const condition = useSelector(selectCondition);

    function digest(num) {
        const arrNum = ["число", "числа", "чисел"];
        let result = [];
        num = String(num);

        if (num >= 20) {
            num = +num.slice(num.length - 1, num.length);
        } else if (num < 20) {
            num = +num.slice(num.length - 2, num.length);
        }

        if (num <= 20) {
            if (1 < num < 5) result[0] = arrNum[1];
            if (num > 4) result = arrNum[2];
            if (num === 1) result = arrNum[0];
            if (num === 0) result = arrNum[2];
        } else {
            if (num === 0) result = arrNum[2];
            if (1 < num < 5) result = arrNum[1];
            if (num > 4) result = arrNum[2];
            if (num === 1) result = arrNum[0];
        }
        return result;
    }

    function dateTime() {
        let now = moment();
        return now.format('DD.MM.YYYY h:mm:ss a')
    }

    const order = (condition['orderNumber']) ? 'с сортировкой результатов' : 'без сортировки результатов';
    const double = (condition['notDouble']) ? 'без повторов' : 'с возможными повторами';
    const conditionString = `${condition['countNumber']} ${digest(condition['countNumber'])}  в диапазоне от ${condition['from']} до ${condition['to']}, ${order}, ${double}`;

    return (
        <div className={condition.hide}>
            <div className="protocol">
                <table className="table">
                    <tbody className="table-condition">
                        <tr>
                            <td>Условия</td>
                            <td>{conditionString}</td>
                        </tr>
                        <tr>
                            <td>Создание</td>
                            <td>{dateTime()}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default Conditions;