import React from 'react';
import { useDispatch } from 'react-redux';
import { recordCondition } from '../store/conditionSlice';


function RandomForm() {
    const dispatch = useDispatch();

    function formData(event) {

        event.preventDefault();
        let from = event.target.elements.from.value;
        let to = event.target.elements.to.value;
        let countNumber = event.target.elements.countNumber.value;
        let notDouble = event.target.elements.notDouble.checked;
        let orderNumber = event.target.elements.orderNumber.checked;

        let arr = {
            'from': from,
            'to': to,
            'countNumber': countNumber,
            'notDouble': notDouble,
            'orderNumber': orderNumber
        }

        function conditionCheck(data) {

            const min = +data['from'];
            const max = +data['to'];
            let count = +data['countNumber'];
            const notDouble = data['notDouble'];

            let massage = '';

            if (min < 0 || max < 0) { massage = 'Введите значения диапазона больше нуля' };
            if (min > 999999999 || max > 999999999) { massage = 'Введите значения диапазона меньше 999 999 999' };
            if (count > 10000) { massage = 'Количество случайных чисел должно быть не более 10 000' };
            if ((max - min + 1) < count && notDouble) { massage = 'уберите условие "без повторов" или уменьшите количество случайных чисел' }
            if (min >= max) { massage = 'Минимальное значение диапазона должно быть меньше максимального' };
            if (count < 1) { massage = 'количество случайных чисел должно быть больше одного' }

            if (massage !== '') {
                dispatch(recordCondition({ 'hide': 'hide' }))
                alert(massage);
            } else {
                dispatch(recordCondition(arr));
            }
        }
        conditionCheck(arr);
    }

    return (
        <form onSubmit={formData}>
            <div className="row">
                <h5 className="mb-3">Выберите диапазон</h5>
                <div className="col-lg-3">от</div>
                <div className="col-lg-3">
                    <label htmlFor="fromNumber" className="visually-hidden"></label>
                    <input type="number" name="from" className="form-control rand-inp inline" id="fromNumber"
                        placeholder="начало" defaultValue="1" />
                </div>
                <div className="col-lg-1">до</div>
                <div className="col-lg-3">
                    <label htmlFor="toNumber" className="visually-hidden">конец</label>
                    <input type="number" name="to" className="form-control rand-inp inline" id="toNumber" placeholder="конец"
                        defaultValue="100" />
                </div>
            </div>
            <div className="row">
                <h5 className="mb-3">Выберите количество чисел</h5>
                <div className="col-lg-3">Получить чисел</div>
                <div className="col-lg-3">
                    <label htmlFor="countNumber" className="visually-hidden"></label>
                    <input type="number" className="form-control rand-inp inline" name='countNumber' id="countNumber" placeholder="5"
                        defaultValue="5" />
                </div>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name='notDouble' defaultValue="" id="notDoubleNumber" />
                <label className="form-check-label" htmlFor="doubleNumber">
                    Без повторов
                </label>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name='orderNumber' id="orderNumber" />
                <label className="form-check-label" htmlFor="orderNumber" >
                    Сортировать результаты по порядку
                </label>
            </div>
            <div className="d-grid gap-2">
                <button type="submit" className="btn-success mt-2" id="btnResult">Получить случайные
                    числа</button>
            </div>
        </form>);
}

export default RandomForm;