function renderGenerate(data) {
    const min = data['from'];
    const max = data['to'];
    let count = data['countNumber'];
    const notDouble = data['notDouble'];
    const order = data['orderNumber'];
    const arrFull = [];

    if (notDouble) {  //получаем массив без дублей при поднятом флаге notDouble
        for (let i = 0; i < count; i++) {
            let item = getRandomIntInclusive(min, max);
            if (arrFull.includes(item)) {
                count++
            } else {
                arrFull.push(item)
            }
        }
    } else { //получаем массив с дублями 
        for (let i = 0; i < count; i++) {
            let item = getRandomIntInclusive(min, max);
            arrFull.push(item);
        }
    }

    if (order) {   //сортируем массив при поднятом флаге order
        arrFull.sort(function (a, b) {
            return a - b;
        });
    }

    const arrSmall = [];

    arrFull.map((elem, index) => {
        if (index < 10) { arrSmall[index] = elem }
        if (index === 10) { arrSmall[index] = '...' }
        return true;
    });

    return [arrFull, arrSmall];
}

/**
 * генерируем случайное чилос из указанного диапазона
 */

function getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min; //Максимум и минимум включаются
}

export default renderGenerate;
