import React from 'react';
import { useSelector } from 'react-redux';
import {
    selectArrayGenerate,
    selectArrayStatistic,
    selectHideStatisticBlock,
    selectHideArrayBlock
} from '../store/conditionSlice';
import { useState } from 'react';


function FullReport() {
    const [hide, setHide] = useState('hide');
    const [hideStatistic, setHideStatistic] = useState('hide');
    const fullArr = useSelector(selectArrayGenerate)[0];
    const statisticArr = useSelector(selectArrayStatistic);
    const hideStatisticBlock = useSelector(selectHideStatisticBlock);
    const hideArrayBlock = useSelector(selectHideArrayBlock);
    // console.log(statisticArr)



    function fullReportViev() {
        if (hide === 'hide') {
            setHide('');
        } else {
            setHide('hide');
        }
    }

    function statisticDoubleViev() {
        if (hideStatistic === 'hide') {
            setHideStatistic('');
        } else {
            setHideStatistic('hide');
        }
    }


    let statisticText;
    if (hideStatisticBlock === "hide") {
        statisticText = 'Повторяющихся чисел для вывода статистики нет'
    } else {
        statisticText = 'Есть данные по повторяющимся случайным числам'
    }

    return (
        < >
            <div className="col-lg-6 mt-5">
                <div className={hideArrayBlock}>
                    <p className='mt-3 bold'>{statisticText}</p>
                </div>
                <div className={hideStatisticBlock}>
                    <div className="full-report">
                        <div className="mt-2 d-grid gap-2">
                            <button className="bt" onClick={statisticDoubleViev}>Показать статистику</button>
                        </div>
                        <div >
                            <div className={hideStatistic} >
                                <table className="table table-hover">
                                    <thead>
                                        <tr>
                                            <th scope="col">№ </th>
                                            <th scope="col">случайные числа</th>
                                            <th scope="col">кол-во повторений</th>
                                            <th scope="col">кол-во уникальных чисел</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {statisticArr.map((elem, index) =>
                                            <tr key={index}>
                                                <td>{(index + 1)}</td>
                                                <td className='text-success  bold'>{elem[1].join(', ')}</td>
                                                <td className='text-primary '>{elem[0]}</td>
                                                <td className='text-info '>{elem[1].length}</td>
                                            </tr>
                                        )}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-lg-6 mt-5">
                <div className={hideArrayBlock}>
                    <div className="full-report">
                        <p className='mt-3 bold'>Полный отчет по случайно полученным числам</p>
                        <div className="mt-2 d-grid gap-2">
                            <button onClick={fullReportViev}>Полный отчет</button>
                        </div>
                        <div className={hide}>
                            <div className="row contein-item" >
                                {fullArr.map((elem, index) =>
                                    <div className="col-lg-2 block-item" key={index}>
                                        <span className="index-arr">{index}</span>
                                        <span className="item-arr fs-2" >{elem}</span>
                                    </div>)}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default FullReport;