function arrHandler(arr, maxHits, order) {
    /*
    *  objCount получаем ассоциативный массив число - колличество повторений
    **/
    let objCount = {};

    arr.map(elem => {
        if (objCount[elem]) {
            objCount[elem]++
        } else {
            objCount[elem] = 1;
        }
        return true
    })

    /*
     *  objResult получаем ассоциативный массив колличество повторений - числа которые повторялись
    **/
    let objResult = {};

    for (let key in objCount) {
        if (objResult[objCount[key]]) {
            objResult[objCount[key]].push(key);
        } else {
            objResult[objCount[key]] = [];
            objResult[objCount[key]].push(key)
        }
    }

    /*
    *  arrMain массив количества повторений
    **/

    let arrMain = [];
    for (let key in objResult) {
        if (key > 1) {
            arrMain.push(key);
        }
    }

    if (order === "ASC") {
        arrMain.sort((a, b) => {
            return a - b;
        })
    } else {
        arrMain.sort((a, b) => {
            return b - a;
        })
    }

    if (maxHits > arrMain.length) maxHits = arrMain.length;

    /*
    *  result массив количества повторений
    **/

    let result = [];
    for (let i = 0; i < maxHits; i++) {
        result.push([arrMain[i], objResult[arrMain[i]]]);
    }

    return result;
}

export default arrHandler;