import React from 'react';
import { useSelector } from 'react-redux';
import { selectCondition, selectArrayGenerate } from '../store/conditionSlice';

function Generate() {
    const condition = useSelector(selectCondition);
    const smallArr = useSelector(selectArrayGenerate)[1];

    return (
        <div className={condition.hide}>
            <div className="border mt-4 ">
                <div className="text-wrap">
                    <p className="text-center fs-2 out">{smallArr.join(', ')}</p>
                </div>
            </div>
        </div>
    )
}

export default Generate;