import RandomForm from "../Random/Form";
import Conditions from "../Random/Conditions";
import FullReport from "../Random/FullReport";
import Generate from "../Random/Generate";

function RandomGenerate() {
    return (
        <div className="container">
            <h4 className="mb-3">Введите данные и получите случайные числа</h4>
            <div className="row ">
                <div className="col-lg-6">
                    <RandomForm />
                </div>
                <div className="col-lg-6 result-random">
                    <Conditions />
                    <Generate />
                </div>
            </div>
            <div className="row">
                <FullReport />
            </div>
        </div>
    );
}

export default RandomGenerate;