import { createSlice } from '@reduxjs/toolkit';
import renderGenerate from './generateArr';
import arrHandler from './statisticArr';

export const conditonSlice = createSlice({
    name: 'condition',
    initialState: {
        value: { 'hide': 'hide' },
        arr: [],
        resultArr: {
            smallArr: [],
            fullArr: []
        },
        arrayGenerate: [[], []],
        arrayStatistic: [],
        hideArrayBlock: "hide",
        hideStatisticBlock: "hide"
    },
    reducers: {
        recordCondition: (state, data) => {
            state.value = data.payload;
            state.arrayGenerate = renderGenerate(data.payload);
            state.arrayStatistic = arrHandler(state.arrayGenerate[0], 10);
            if (state.arrayGenerate[0] !== undefined) {
                state.hideArrayBlock = '';
            }
            if (state.arrayStatistic[0] !== undefined) {
                state.hideStatisticBlock = '';
            } else {
                state.hideStatisticBlock = 'hide';
            }
        },
        arrRandomRecord: (state, data) => {
            state.arr = data.payload;
        },
        resultSmallArr: (state, data) => {
            state.resultArr.smallArr = data.payload;
        },
        resultFullArr: (state, data) => {
            state.resultArr.fullArr = data.payload;
        }
    }
});

export const { recordCondition, arrRandomRecord, resultSmallArr, resultFullArr } = conditonSlice.actions;
export const selectCondition = state => state.condition.value;
export const selectArr = state => state.condition.resultArr;
export const selectArrayGenerate = state => state.condition.arrayGenerate;
export const selectRandomArr = state => state.condition.arr;
export const selectArrayStatistic = state => state.condition.arrayStatistic;
export const selectHideStatisticBlock = state => state.condition.hideStatisticBlock;
export const selectHideArrayBlock = state => state.condition.hideArrayBlock;
export default conditonSlice.reducer;
